import React from "react";
import {Container, Form, Button} from "react-bootstrap";
import emailjs from "emailjs-com";
import * as EmailValidator from 'email-validator';
import {motion} from "framer-motion";


export default class Contact extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            mail: "",
            message: "",
            errorMail: null,
            errorMessage: null,
            success: null
        };
        emailjs.init("user_XrLlGT2tzjjUMlVcFih6j");
    }

    sendMail = () => {
        const template = {
            mail: this.state.mail,
            message: this.state.message
        };
        this.setState({errorMail: null, errorMessage: null, success: null});
        if(template.message.length < 10)
            this.setState({errorMessage: "Message trop court!"});
        if (EmailValidator.validate(template.mail))
            if(template.message.length > 10) {
                emailjs.send('gmail', 'template_xoJGSbQk', template)
                    .then((response) => {
                        this.setState({errorMail: null, errorMessage: null, success: "Mail envoye!"});
                    }, (error) => {
                        this.setState({errorMail: "Erreur lors de l'envoi!", errorMessage: null, success: null});
                    });
            }
            else this.setState({errorMessage: "Message trop court!"});
        else this.setState({errorMail: "Mail invalide!"});
    }

    render() {
        return (<Container id={"contact"} style={style.container}>
            <h2>Me contacter:</h2>
            <Form style={style.form}>
                    {this.state.success && <h3 className={"text-success"}>{this.state.success}</h3>}
                    {this.state.errorMail && <h3 className={"text-danger"}>{this.state.errorMail}</h3>}
                    <Form.Label>Votre mail:</Form.Label>
                    <Form.Control style={style.formControl} placeholder="Votre mail" onChange={(e) => this.setState({mail: e.target.value})}/>
                    {this.state.errorMessage && <h3 className={"text-danger"}>{this.state.errorMessage}</h3>}
                    <Form.Label>Votre message:</Form.Label>
                    <Form.Control placeholder="Contenu" as={"textarea"}
                                  onChange={(e) => this.setState({message: e.target.value})}/>
            </Form>
            <motion.button whileHover={{scale: 1.1}} whileTap={{scale: 0.9}} onClick={this.sendMail}>Envoyer</motion.button>
        </Container>);
    }
}

const style = {
    container: {
        marginTop: "200px",
        paddingBottom: "50px",
        paddingTop: "50px",
        fontSize: "14px"
    },
    form: {
        marginTop: "20px"
    },
    formControl: {
        marginBottom: "20px"
    }
}