import React from "react";
import {Container, Row, Col, Card, ListGroup, ListGroupItem, Button} from "react-bootstrap";
import {ParallaxBanner} from "react-scroll-parallax/cjs";
import MainConnect from "../connect4/MainConnect";
import delay from "delay";

export default class Projet extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            c4: null
        };
    }

    showC4 = async () => {
        this.setState({c4: false});
        // await delay(200);
        window.scrollTo({ behavior: 'smooth', top:  document.querySelector("#propos").offsetTop});
    }

    render() {
        return (<Container classname={"text-danger"} id={"projet"} style={style.container} fluid>
            <ParallaxBanner layers={[
                {
                    image: require("../../images/Fond3.jpg"),
                    amount: 0.4,
                    children: <h1 style={style.title}>Liste de projets fais au cours de mon annee:</h1>
                }
            ]}/>
            <p style={{marginTop: "20px", marginBottom: "20px"}}/>
            {this.state.c4 && <>
                <MainConnect/>
                <h3 className={"text-danger"} style={{cursor: "pointer"}} onClick={this.showC4}>
                    Fermer
                </h3>
            </>}
            <Row>
                {projets.map((x, i) => (<Col md={6} lg={4}>
                    <Card>
                        <Card.Img alt={"Image du projet"} variant="top" src={x.image}/>
                        <Card.Body>
                            <Card.Title><h2>{x.name}</h2></Card.Title>
                            <Card.Text>
                                {x.description}
                            </Card.Text>
                        </Card.Body>
                        <Card.Body>
                            <Row>
                                <Col>
                                    <Card.Link href={x.link}><h2>Github</h2></Card.Link>
                                </Col>
                                {x.test && <Col>
                                    <Card.Link style={style.link} onClick={() => this.setState({c4: true})}>
                                        <h2>Tester</h2>
                                    </Card.Link>
                                </Col>}
                            </Row>
                        </Card.Body>
                    </Card>
                </Col>))}
            </Row>
        </Container>);
    }
}

const style = {
    title: {
        position: "absolute",
        top: "50%",
        left: "35%",
        color: "#626980"
    },
    link: {
        cursor: "pointer",
        color: "#117BFF"
    },
    container: {
        marginTop: "50px",
        paddingBottom: "50px",
        paddingTop: "50px"
    }
}

const projets = [
    {
        name: "Puissance 4",
        image: "/images/Fond1.jpg",
        link: "",
        description: "Creation d'un puissance 4 en plugin jQuery avec implementation de plusieurs types d'ia.",
        test: true
    },
    {
        name: "Mini framework MVC",
        image: "/images/Fond2.jpg",
        link: "https://github.com/MohammedBenhelli/COVID-19/tree/master/MVC_Pie_PHP",
        description: "Framework PHP leger et complet cree from scratch en s'inspirant de Laravel",
        test: null
    },
]