import React from "react";
import $ from "jquery";
import "./puissance4";
import {Row, Col, Button, Form} from "react-bootstrap";

export default class MainConnect extends React.Component {
    constructor(props) {
        super(props);
        let x = 7;
        let y = 6;
        let player1Color = "#ff7329";
        let player2Color = "#47c908";
        let difficulty = "none";
        let difficulty1 = "hard";
        setTimeout(() => $("#board").puissance_4(x, y, player1Color, player2Color, difficulty, difficulty1), 1000);
    }

    render() {
        return (<Row>
            <Col>
                <table>
                    <tbody className="twelve columns" id="board"/>
                </table>
            </Col>
            <Col>
                <div id="play1">
                    <h3>
                        Player 1: <a style={{color: "lightgreen"}} id='player1win'>0</a> wins
                    </h3>
                </div>
                <div id="play2">
                    <h3>
                        Player 2: <a style={{color: "lightgreen"}} id='player2win'>0</a> wins
                    </h3>
                </div>
                <div style={{color: "indianred"}} id='error'/>
                <div id='playerTurn'>player 1 turn</div>
                <Button id={"back"} variant={"light"}>Back</Button>
                <Form>
                    <Form.Group>
                        <Form.Label>Changez de difficulte du Joueur 1</Form.Label>
                        <Form.Control id={"difficulty1"} as="select" custom>
                            <option value={"r"}>Choisir</option>
                            <option value={"none"}>Aucune (Joueur 1)</option>
                            <option value={"easy"}>Facile</option>
                            <option value={"normal"}>Moyen (Minimax)</option>
                            <option value={"hard"}>Dificile (Monte Carlo)</option>
                        </Form.Control>
                    </Form.Group>
                </Form>
                <Form>
                    <Form.Group>
                        <Form.Label>Changez de difficulte du Joueur 2</Form.Label>
                        <Form.Control id={"difficulty2"} as="select" custom>
                            <option value={"r"}>Choisir</option>
                            <option value={"none"}>Aucune (Joueur 2)</option>
                            <option value={"easy"}>Facile</option>
                            <option value={"normal"}>Moyen (Minimax)</option>
                            <option value={"hard"}>Dificile (Monte Carlo)</option>
                        </Form.Control>
                    </Form.Group>
                </Form>
            </Col>
        </Row>)
    }
}