import React from "react";
import {Button, Container, Row, Col} from "react-bootstrap";
import {Document, Page, pdfjs} from "react-pdf";
import {saveAs} from "file-saver";
import * as pdf from "../../CV.pdf";
import Axios from "axios";
import delay from "delay";
import {motion} from "framer-motion";

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

export default class Propos extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showCv: null,
            samsung: ""
        };
    }

    componentDidMount() {
        this.writeSamsung();
    }

    writeSamsung = async () => {
        const tab = "Samsung Campus.".split("");
        for (let i = 0; i < tab.length; i++) {
            await delay(200);
            this.setState({samsung: `${this.state.samsung}${tab[i]}`});
        }
    }

    dlCv = async () => {
        const dowload = await Axios({
            method: "get",
            url: "http://localhost:3000" + pdf,
            responseType: "blob"
        });
        console.log(dowload.data);
        saveAs(dowload.data, "CV Mohammed Benhelli.pdf");
    }

    openSamsung = () => {
        window.open("https://campus.samsung.fr/", '_blank').focus();
    }

    render() {
        return (<Container id={"propos"} style={{marginTop: "200px"}}>
            <Row style={{marginBottom: "50px"}}>
                <h2>Bonjour je m'appelle Mohammed Benhelli et je suis etudiant au
                    <i style={style.link} onClick={this.openSamsung}>
                        {this.state.samsung}
                    </i>
                </h2>
                <hr/>
                <h3>Ce portfolio a pour but de m'aider dans la recherche d'un alternance en developpement web.</h3>
            </Row>
            {
                !this.state.showCv && <>
                    <Row>
                        <Col>
                            <motion.button whileHover={{scale: 1.1}} whileTap={{scale: 0.9}}
                                           onClick={() => this.setState({showCv: true})}>
                                Voir mon CV
                            </motion.button>
                        </Col>
                        <Col>
                            <motion.button whileHover={{scale: 1.1}} whileTap={{scale: 0.9}} onClick={this.dlCv}>
                                Telecharger mon CV
                            </motion.button>
                        </Col>
                    </Row>
                </>
            }
            {
                this.state.showCv && <>
                    <motion.button whileHover={{scale: 1.1}} whileTap={{scale: 0.9}} variant={"danger"}
                                   style={style.closeBtn} onClick={() => this.setState({showCv: null})}>
                        X
                    </motion.button>
                    <Document
                        file={pdf}
                    >
                        <Page pageNumber={1} width={window.innerWidth / 1.5}/>
                    </Document>
                </>
            }
        </Container>);
    }
}

const style = {
    link: {
        cursor: "pointer",
        color: "orange",
        marginLeft: "5px"
    },
    closeBtn: {
        backgroundColor: "#E74C3C",
        marginBottom: "5px"
    }
}