import React from "react";
import {Container, Row, Col, ProgressBar} from "react-bootstrap";
import FontAwesome from "react-fontawesome";
import {Parallax} from "react-scroll-parallax/cjs";

export default class Competence extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        return (<Parallax y={[-70, 20]} tagOuter={"figure"}>
            <Container id={"competence"} style={style.container} fluid>
                <Row>
                    <Col style={style.barre}>
                        <h2>Domaines de competences</h2>
                        <hr/>
                        <h4>Dynamisation des sites avec du javascript</h4>
                        <h4>Integration HTML/CSS</h4>
                        <h4>Validation W3C</h4>
                        <h4>Conception de site responsive</h4>
                    </Col>
                    <Col>
                        <h2>Skills en developpement</h2>
                        <hr style={style.hr}/>
                        <Row style={style.row}>
                            <Col lg={5}>
                                <i className={"fab fa-3x fa-html5 text-danger"}> HTML</i>
                            </Col>
                            <Col lg={7}>
                                <ProgressBar variant={"danger"} now={100}/>
                            </Col>
                        </Row>
                        <Row>
                            <Col lg={5}>
                                <i className={"fab fa-3x fa-css3-alt text-primary"}> CSS/SASS</i>
                            </Col>
                            <Col lg={7}>
                                <ProgressBar variant={"primary"} now={60}/>
                            </Col>
                        </Row>
                        <Row>
                            <Col lg={5}>
                                <i className={"fab fa-3x fa-js text-warning"}> Javascript</i>
                            </Col>
                            <Col lg={7}>
                                <ProgressBar variant={"warning"} now={80}/>
                            </Col>
                        </Row>
                        <Row>
                            <Col lg={5}>
                                <i className={"fab fa-3x fa-php text-php"}> PHP</i>
                            </Col>
                            <Col lg={7}>
                                <ProgressBar variant={"php"} now={70}/>
                            </Col>
                        </Row>
                        <hr style={style.hr}/>
                        <h2>Frameworks abords</h2>
                        <Row style={style.row}>
                            <Col>
                                <img src={require("../../images/laravel.png")} className={"img-fluid"} alt="laravel icon"/>
                            </Col>
                            <Col>
                                <h1 style={{color: "#8F251E"}}>Laravel</h1>
                            </Col>
                        </Row>
                    </Col>
                </Row>
                <style type={"text/css"}>
                    {`
                    .text-php {
                        color: #7478AE;
                    }
                    
                    .bg-php {
                        background-color: #7478AE !important;
                    }
                `}
                </style>
            </Container>
        </Parallax>);
    }
}

const style = {
    barre: {
        borderRight: "1px solid #8F8F8F"
    },
    container: {
        marginTop: "200px"
    },
    hr: {
        border: "1px solid white"
    },
    row: {
        marginTop: "20px"
    }
}