import React from "react";
import {Navbar, Nav} from "react-bootstrap";

export default class NavPortfolio extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    scrollCompetence = () => {
        window.scrollTo({ behavior: 'smooth', top:  document.querySelector("#competence").offsetTop});
    }

    scrollPropos = () => {
        window.scrollTo({ behavior: 'smooth', top:  document.querySelector("#propos").offsetTop});
    }

    scrollProjet = () => {
        window.scrollTo({ behavior: 'smooth', top:  document.querySelector("#projet").offsetTop});
    }

    scrollContact= () => {
        window.scrollTo({ behavior: 'smooth', top:  document.querySelector("#contact").offsetTop});
    }

    render() {
        return (
            <Navbar bg={"dark"} variant={"dark"}  sticky={"top"}>
                <Navbar.Brand href="#home"><h1>Mohammed Benhelli</h1></Navbar.Brand>
                <Nav>
                    <Nav.Link className={"text-white"} onClick={this.scrollPropos}><h4>A Propos</h4></Nav.Link>
                    <Nav.Link className={"text-white"} onClick={this.scrollCompetence}><h4>Competences</h4></Nav.Link>
                    <Nav.Link className={"text-white"} onClick={this.scrollProjet}><h4>Mes Projets</h4></Nav.Link>
                    <Nav.Link className={"text-white"} onClick={this.scrollContact}><h4>Contact</h4></Nav.Link>
                </Nav>
            </Navbar>
        );
    }
}